.sales {
  padding: 120px 0;
  overflow: hidden;

  .rv-button {
    justify-content: center;
    margin-top: auto;
    width: 100%;
  }

  &__text {
    margin-bottom: 16px;
  }

  &__title {
    margin-bottom: 16px;
    font-family: "Causten-600", sans-serif;
    font-weight: 600;
    font-size: 48px;
    line-height: 1.2;
    letter-spacing: 0.015em;
    text-transform: uppercase;
    color: #fff;
    transition: all 0.3s;

    &:hover {
      color: #fbd394;
    }

    @include m($xl) {
      font-size: 42px;
    }

    @include m($lg) {
      font-size: 38px;
    }
  }

  .sales-inner {
    display: flex;
    justify-content: space-between;

    @include m($lg) {
      flex-direction: column;
    }
  }

  .sales-slide {
    opacity: 0;
    animation: fade 0.9s forwards;

    &:not(:last-child) {
      margin-bottom: 80px;
    }
  }

  &__info {
    z-index: 10;
    display: flex;
    flex-direction: column;
    margin-left: 48px;
    width: 100%;
    max-width: 485px;

    h3 {
      margin-bottom: 16px;
      color: #fbd394;

      @include m($xl) {
        font-size: 24px;
      }

      @include m($lg) {
        font-size: 24px;
      }
    }

    .sale__tags {
      flex-wrap: wrap;
      margin-bottom: 0;

      div {
        margin-right: 16px;
        margin-bottom: 16px;

        + div {
          margin-left: 0;
        }
      }

      p {
        margin-bottom: 0;
        font-family: "Causten", sans-serif;
        font-weight: 400;
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.04em;
        color: #b5b1ad;
      }
    }

    @include m($lg) {
      margin: 25px 0 0;
      max-width: 100%;
    }
  }

  .sales-swiper {
    position: relative;
    margin-left: 0;
    max-width: 1019px;

    @include m(1600) {
      max-width: 900px;
    }

    @include m(1400) {
      max-width: 800px;
    }

    @include m(1280) {
      max-width: 600px;
    }

    @include m($lg) {
      max-width: 100%;
      max-height: 500px;
      overflow: hidden;
    }
  }

  .swiper-slide {
    position: relative;
    opacity: 0.06;
    margin-right: auto;
    min-height: 639px;
    max-width: 1019px;
    max-height: 639px;
    transform: scale(0.8);
    transition: all 0.3s;

    &::before {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, rgba(0, 0, 0, 0) 50.81%, rgba(0, 0, 0, 0.5) 100%);
    }

    img {
      width: 100%;
      height: 100%;
      min-height: 639px;
      max-height: 639px;
      object-fit: cover;

      @include m(1600) {
        min-height: 540px;
        max-height: 540px;
      }

      @include m(1400) {
        min-height: 490px;
        max-height: 490px;
      }

      @include m($xl) {
        min-height: 460px;
        max-height: 460px;
      }

      @include m($lg) {
        min-height: 450px;
        max-height: 450px;
      }

      @include m($md) {
        min-height: 350px;
        max-height: 350px;
      }

      @include m($sm) {
        min-height: 300px;
        max-height: 300px;
      }
    }

    @include m(1600) {
      min-height: 540px;
      max-height: 540px;
    }

    @include m(1400) {
      min-height: 490px;
      max-height: 490px;
    }

    @include m($xl) {
      min-height: 460px;
      max-height: 460px;
    }

    @include m($lg) {
      min-height: 450px;
      max-height: 450px;
    }

    @include m($md) {
      min-height: 350px;
      max-height: 350px;
    }

    @include m($sm) {
      min-height: 300px;
      max-height: 300px;
    }
  }

  .swiper-slide-active {
    opacity: 1;
    height: 100%;
    transform: scale(1);
  }

  .swiper-pagination {
    left: 48px;
    bottom: 56px;
    display: flex;
    align-items: flex-end;
    width: fit-content;
    height: 8px;
    text-align: left;

    @include m($lg) {
      left: 16px;
      bottom: 24px;
    }
  }

  .swiper-pagination-bullet {
    display: block;
    opacity: 1;
    margin: 0 !important;
    padding: 0;
    width: 6px;
    height: 6px;
    border-radius: 0;
    font-size: 0;
    line-height: 1;
    background: #d9d9d9;
    transition: all 0.3s;

    &:not(:last-child) {
      margin-right: 8px !important;
    }
  }

  .swiper-pagination-bullet-active {
    width: 6px;
    height: 8px;
    border-radius: 10px 10px 0 0;
    background-color: #f3d697;
  }

  .swiper-button-next,
  .swiper-button-prev {
    top: auto;
    bottom: 48px;
    width: 55px;
    height: 24px;
    font-size: 0;
    line-height: 1;

    &::before {
      position: absolute;
      left: 50%;
      top: 50%;
      width: 42px;
      height: 8px;
      transform: translate(-50%, -50%);
    }

    &::after {
      display: none;
    }

    @include m($lg) {
      bottom: 16px;
    }
  }

  .swiper-button-next {
    right: 48px;

    &::before {
      content: url("../images/swiper-sales-right.svg");
    }

    @include m($lg) {
      right: 16px;
    }
  }

  .swiper-button-prev {
    left: auto;
    right: 127px;

    &::before {
      content: url("../images/swiper-sales-left.svg");
    }

    @include m($lg) {
      right: 95px;
    }
  }
}